import { toDoFactory } from "./factories";
import { UI } from "./UI";
import { defaultLoad } from "./defaultLoad";

export function addTaskHandler() {
  UI.ADDTASK.addEventListener("click", () => {
    if (UI.TASKTEXT.value === "") {
    } else {
      UI.POPUP.classList.add("active");
      UI.OVERLAY.classList.add("active");
      console.log(UI.TASKTEXT.value);
      let task = toDoFactory(`${UI.TASKTEXT.value}`, "", "&#128176", false);
      defaultLoad.firstProject.todos.push(task);
      console.log(defaultLoad.firstProject.todos[0].description);
    }
  });
}
